.consult {
  display: flex;
}

.consult_form {
  width           : 398px;
  min-height      : 443px;
  border-radius   : 10px;
  background-color: rgba(239, 203, 187, 0.4);
  padding-top     : 79px;
  /* padding-left : 32px; */
}

.consult_title {
  font-size     : 20px;
  letter-spacing: 0px;
  color         : #48443e;
  font-weight   : 600;
  font-family   : 'Montserrat';
  text-align    : center;
  text-transform: uppercase;
  margin        : 0px 0px 35px 0px;
}

.consult_form_box {
  padding   : 0px 32px 0px 32px;
  max-width : 100%;
  text-align: center;
}

.consult_form_input {
  display         : flex;
  background-color: transparent;
  border          : none;
  border-bottom   : 1px solid rgba(72, 67, 61, 0.2);
  width           : 100%;
  margin-bottom   : 30px;
  transition      : .5s all ease;
}

.consult_form_input:hover,
.consult_form_input:focus,
.consult_form_input:active {
  outline       : 0;
  outline-offset: 0;
}

.consult_form_input::placeholder {
  font-size     : 14px;
  letter-spacing: 0px;
  line-height   : 18px;
  color         : #48443e;
  font-weight   : 400;
  font-family   : 'Open Sans';
  padding-bottom: 5px;
}

.consult_form_button {
  font-size       : 14px;
  letter-spacing  : 0px;
  color           : #ffffff;
  font-weight     : 500;
  font-family     : 'Montserrat';
  text-align      : center;
  text-transform  : uppercase;
  background-color: #2e333c;
  padding         : 14px;
  margin-top      : 50px;
  border          : none;
  transition      : .5s all ease;
}

.consult_form_button:hover {
  background-color: rgba(46, 51, 60, .5);
}

.consult_popular {
  background-image: url(../../res/images/popular-treatments.jpg);
  width           : 68%;
  justify-items   : right;
  padding-top     : 75px;
  padding-left    : 133px;
}

.consult_popular_title {
  font-size     : 32px;
  letter-spacing: 0px;
  color         : #48443e;
  font-weight   : 600;
  font-family   : 'Montserrat';
  text-transform: uppercase;
  margin        : 0px 0px 25px 0px;
}

.consult_popular_text {
  max-width     : 288px;
  min-height    : 124px;
  font-size     : 12px;
  letter-spacing: 0px;
  line-height   : 16px;
  color         : #48443e;
  font-weight   : 400;
  font-family   : 'Open Sans';
  text-align    : left;
  margin        : 0px 0px 40px 0px;
}

.consult_popular_button {
  border          : none;
  background-color: #e2b29a;
  text-transform  : uppercase;
  width           : 288px;
  padding         : 16px 44px;
  font-size       : 14px;
  letter-spacing  : 0px;
  color           : #ffffff;
  font-weight     : 500;
  font-family     : 'Montserrat';
  text-align      : center;
  transition      : .5s all ease;
}

.consult_popular_button:hover {
  background-color: #efcbbb;
}